	module.exports = {"data":{"postsJson":{"title":"Tried, Crucified & Risen","questionsTitle":"OPENING THE WORD","display":"block","bookName":"Luke","ref":"23:1-24:53","html":"<p>   <strong>23</strong><u>1</u>The whole company of them rose up and brought him before Pilate. <u>2</u>They began to accuse him, saying,</p><h1>THE JEWISH COUNCIL</h1><h4>We found this man perverting the nation, forbidding paying taxes to Caesar, and saying that he himself is Christ, a king.</h4><p>   <u>3</u>Pilate asked him,</p><h1>PONTIUS PILATE</h1><h4>Are you the King of the Jews?</h4><p>   He answered him,</p><h1>JESUS</h1><h2>So you say.</h2><p>   <u>4</u>Pilate said to the chief priests and the multitudes,</p><h1>PONTIUS PILATE</h1><h4>I find no basis for a charge against this man.</h4><p>   <u>5</u>But they insisted, saying,</p><h1>THE JEWISH COUNCIL</h1><h4>He stirs up the people, teaching throughout all Judea, beginning from Galilee even to this place.</h4><p><u>6</u>But when Pilate heard Galilee mentioned, he asked if the man was a Galilean. <u>7</u>When he found out that he was in Herod’s jurisdiction, he sent him to Herod, who was also in Jerusalem during those days.<br />   <u>8</u>Now when Herod saw Jesus, he was exceedingly glad, for he had wanted to see him for a long time, because he had heard many things about him. He hoped to see some miracle done by him. <u>9</u>He questioned him with many words, but he gave no answers. <u>10</u>The chief priests and the scribes stood, vehemently accusing him. <u>11</u>Herod with his soldiers humiliated him and mocked him. Dressing him in luxurious clothing, they sent him back to Pilate. <u>12</u>Herod and Pilate became friends with each other that very day, for before that they were enemies with each other.<br />   <u>13</u>Pilate called together the chief priests and the rulers and the people, <u>14</u>and said to them,</p><h1>PONTIUS PILATE</h1><h4>You brought this man to me as one that perverts the people, and see, I have examined him before you, and found no basis for a charge against this man concerning those things of which you accuse him. <u>15</u>Neither has Herod, for I sent you to him, and see, nothing worthy of death has been done by him. <u>16</u>I will therefore chastise him and release him.</h4><p>   <u>17</u>Now he had to release one prisoner to them at the feast. <u>18</u>But they all cried out together, saying,</p><h1>THE CROWD</h1><h4>Away with this man! Release to us Barabbas!</h4><p>— <u>19</u>one who was thrown into prison for a certain revolt in the city, and for murder.<br />   <u>20</u>Then Pilate spoke to them again, wanting to release Jesus, <u>21</u>but they shouted, saying,</p><h1>THE CROWD</h1><h4>Crucify! Crucify him!</h4><p>   <u>22</u>He said to them the third time,</p><h1>PONTIUS PILATE</h1><h4>Why? What evil has this man done? I have found no capital crime in him. I will therefore chastise him and release him.</h4><p><u>23</u>But they were urgent with loud voices, asking that he might be crucified. Their voices and the voices of the chief priests prevailed. <u>24</u>Pilate decreed that what they asked for should be done. <u>25</u>He released him who had been thrown into prison for insurrection and murder, for whom they asked, but he delivered Jesus up to their will.<br />   <u>26</u>When they led him away, they grabbed one Simon of Cyrene, coming from the country, and laid on him the cross, to carry it after Jesus. <u>27</u>A great multitude of the people followed him, including women who also mourned and lamented him. <u>28</u>But Jesus, turning to them, said,</p><h1>JESUS</h1><h2>Daughters of Jerusalem, don’t weep for me, but weep for yourselves and for your children. <u>29</u>For behold, the days are coming in which they will say, ‘Blessed are the barren, the wombs that never bore, and the breasts that never nursed.’ <u>30</u>Then they will begin to tell the mountains, ‘Fall on us!’ and tell the hills, ‘Cover us.’* <u>31</u>For if they do these things in the green tree, what will be done in the dry?</h2><p>   <u>32</u>There were also others, two criminals, led with him to be put to death. <u>33</u>When they came to the place that is called The Skull, they crucified him there with the criminals, one on the right and the other on the left.<br />   <u>34</u>Jesus said,</p><h1>JESUS</h1><h2>Father, forgive them, for they don’t know what they are doing.</h2><p>   Dividing his garments among them, they cast lots. <u>35</u>The people stood watching. The rulers with them also scoffed at him, saying,</p><h1>JEWISH LEADERS</h1><h4>He saved others. Let him save himself, if this is the Christ of God, his chosen one!</h4><p>   <u>36</u>The soldiers also mocked him, coming to him and offering him vinegar, <u>37</u>and saying,</p><h1>ROMAN SOLDIERS</h1><h4>If you are the King of the Jews, save yourself!</h4><p>   <u>38</u>An inscription was also written over him in letters of Greek, Latin, and Hebrew: “THIS IS THE KING OF THE JEWS.”<br />   <u>39</u>One of the criminals who was hanged insulted him, saying,</p><h1>THE UNREPENTANT THIEF</h1><h4>If you are the Christ, save yourself and us!</h4><p>   <u>40</u>But the other answered, and rebuking him said,</p><h1>THE REPENTANT THIEF</h1><h4>Don’t you even fear God, seeing you are under the same condemnation? <u>41</u>And we indeed justly, for we receive the due reward for our deeds, but this man has done nothing wrong.</h4><p><u>42</u>He said to Jesus,</p><h1>THE REPENTANT THIEF</h1><h4>Lord, remember me when you come into your Kingdom.</h4><p>   <u>43</u>Jesus said to him,</p><h1>JESUS</h1><h2>Assuredly I tell you, today you will be with me in Paradise.</h2><p>   <u>44</u>It was now about the sixth hour,* and darkness came over the whole land until the ninth hour.* <u>45</u>The sun was darkened, and the veil of the temple was torn in two. <u>46</u>Jesus, crying with a loud voice, said,</p><h1>JESUS</h1><h2>Father, into your hands I commit my spirit!</h2><p>Having said this, he breathed his last.<br />   <u>47</u>When the centurion saw what was done, he glorified God, saying,</p><h1>A ROMAN SOLDIER</h1><h4>Certainly this was a righteous man.</h4><p><u>8</u>All the multitudes that came together to see this, when they saw the things that were done, returned home beating their breasts. <u>49</u>All his acquaintances, and the women who followed with him from Galilee, stood at a distance, watching these things.<br />   <u>50</u>Behold, a man named Joseph, who was a member of the council, a good and righteous man <u>51</u>(he had not consented to their counsel and deed), from Arimathaea, a city of the Jews, who was also waiting for God’s Kingdom: <u>52</u>this man went to Pilate, and asked for Jesus’ body. <u>53</u>He took it down, and wrapped it in a linen cloth, and laid him in a tomb that was cut in stone, where no one had ever been laid. <u>54</u>It was the day of the Preparation, and the Sabbath was drawing near. <u>55</u>The women, who had come with him out of Galilee, followed after, and saw the tomb, and how his body was laid. <u>56</u>They returned, and prepared spices and ointments. On the Sabbath they rested according to the commandment.<br />   <strong>24</strong><u>1</u>But on the first day of the week, at early dawn, they and some others came to the tomb, bringing the spices which they had prepared. <u>2</u>They found the stone rolled away from the tomb. <u>3</u>They entered in, and didn’t find the Lord Jesus’ body. <u>4</u>While they were greatly perplexed about this, behold, two men stood by them in dazzling clothing. <u>5</u>Becoming terrified, they bowed their faces down to the earth.<br />   They said to them,</p><h1>TWO ANGELS</h1><h4>Why do you seek the living among the dead? <u>6</u>He isn’t here, but is risen. Remember what he told you when he was still in Galilee, <u>7</u>saying that the Son of Man must be delivered up into the hands of sinful men, and be crucified, and the third day rise again?</h4><p>   <u>8</u>They remembered his words, <u>9</u>returned from the tomb, and told all these things to the eleven, and to all the rest. <u>10</u>Now they were Mary Magdalene, Joanna, and Mary the mother of James. The other women with them told these things to the apostles. <u>11</u>These words seemed to them to be nonsense, and they didn’t believe them. <u>12</u>But Peter got up and ran to the tomb. Stooping and looking in, he saw the strips of linen lying by themselves, and he departed to his home, wondering what had happened.<br />   <u>13</u>Behold, two of them were going that very day to a village named Emmaus, which was sixty stadia* from Jerusalem. <u>14</u>They talked with each other about all of these things which had happened. <u>15</u>While they talked and questioned together, Jesus himself came near, and went with them. <u>16</u>But their eyes were kept from recognizing him. <u>17</u>He said to them,</p><h1>JESUS</h1><h2>What are you talking about as you walk, and are sad?</h2><p>   <u>18</u>One of them, named Cleopas, answered him,</p><h1>CLEOPAS</h1><h4>Are you the only stranger in Jerusalem who doesn’t know the things which have happened there in these days?</h4><p>   <u>19</u>He said to them,</p><h1>JESUS</h1><h2>What things?</h2><p>   They said to him,</p><h1>CLEOPAS &amp; CLEOPAS’ COMPANION</h1><h4>The things concerning Jesus, the Nazarene, who was a prophet mighty in deed and word before God and all the people; <u>20</u>and how the chief priests and our rulers delivered him up to be condemned to death, and crucified him. <u>21</u>But we were hoping that it was he who would redeem Israel. Yes, and besides all this, it is now the third day since these things happened. <u>22</u>Also, certain women of our company amazed us, having arrived early at the tomb; <u>23</u>and when they didn’t find his body, they came saying that they had also seen a vision of angels, who said that he was alive. <u>24</u>Some of us went to the tomb, and found it just like the women had said, but they didn’t see him.</h4><p>   <u>25</u>He said to them,</p><h1>JESUS</h1><h2>Foolish men, and slow of heart to believe in all that the prophets have spoken! <u>26</u>Didn’t the Christ have to suffer these things and to enter into his glory?</h2><p><u>27</u>Beginning from Moses and from all the prophets, he explained to them in all the Scriptures the things concerning himself. <u>28</u>They came near to the village, where they were going, and he acted like he would go further.<br />   <u>29</u>They urged him, saying,</p><h1>CLEOPAS &amp; CLEOPAS’ COMPANION</h1><h4>Stay with us, for it is almost evening, and the day is almost over.</h4><p>   He went in to stay with them. <u>30</u>When he had sat down at the table with them, he took the bread and gave thanks. Breaking it, he gave to them. <u>31</u>Their eyes were opened, and they recognized him, and he vanished out of their sight. <u>32</u>They said to one another,</p><h1>CLEOPAS &amp; CLEOPAS’ COMPANION</h1><h4>Weren’t our hearts burning within us, while he spoke to us along the way, and while he opened the Scriptures to us?</h4><p><u>33</u>They rose up that very hour, returned to Jerusalem, and found the eleven gathered together, and those who were with them, <u>34</u>saying,</p><h1>ELEVEN DISCIPLES</h1><h3>The Lord is risen indeed, and has appeared to Simon!</h3><p><u>35</u>They related the things that happened along the way, and how he was recognized by them in the breaking of the bread.<br />   <u>36</u>As they said these things, Jesus himself stood among them, and said to them,</p><h1>JESUS</h1><h2>Peace be to you.</h2><p>   <u>37</u>But they were terrified and filled with fear, and supposed that they had seen a spirit.<br />   <u>38</u>He said to them,</p><h1>JESUS</h1><h2>Why are you troubled? Why do doubts arise in your hearts? <u>39</u>See my hands and my feet, that it is truly me. Touch me and see, for a spirit doesn’t have flesh and bones, as you see that I have.</h2><p><u>40</u>When he had said this, he showed them his hands and his feet. <u>41</u>While they still didn’t believe for joy, and wondered, he said to them,</p><h1>JESUS</h1><h2>Do you have anything here to eat?</h2><p>   <u>42</u>They gave him a piece of a broiled fish and some honeycomb. <u>43</u>He took them, and ate in front of them. <u>44</u>He said to them,</p><h1>JESUS</h1><h2>This is what I told you, while I was still with you, that all things which are written in the law of Moses, the prophets, and the psalms, concerning me must be fulfilled.</h2><p>   <u>45</u>Then he opened their minds, that they might understand the Scriptures. <u>46</u>He said to them,</p><h1>JESUS</h1><h2>Thus it is written, and thus it was necessary for the Christ to suffer and to rise from the dead the third day, <u>47</u>and that repentance and remission of sins should be preached in his name to all the nations, beginning at Jerusalem. <u>48</u>You are witnesses of these things. <u>49</u>Behold, I send out the promise of my Father on you. But wait in the city of Jerusalem until you are clothed with power from on high.</h2><p>   <u>50</u>He led them out as far as Bethany, and he lifted up his hands, and blessed them. <u>51</u>While he blessed them, he withdrew from them, and was carried up into heaven. <u>52</u>They worshiped him, and returned to Jerusalem with great joy, <u>53</u>and were continually in the temple, praising and blessing God. Amen</p><h5>*Luke 23:30 Hosea 10:8<br />*Luke 23:44 Time was counted from sunrise, so the sixth hour was about noon.<br />*Luke 23:44 3:00 PM<br />*Luke 24:13 60 stadia = about 11 kilometers or about 7 miles.</h5>","path":"/338","Question1":"What do you like about this portion of Scripture?","Question2":"What do you observe about the ways of God?","Question3":"What do you observe about the key characters in this portion of Scripture?","Question4":"What response are you wanting to make in light of these observations?","prevPath":"/337","nextPath":"/339"}},"pathContext":{}}

